import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import axios from 'axios';
import React, { useEffect, useState } from 'react';

import { ImArrowUpRight, ImArrowDownRight } from 'react-icons/im';

import logo from './assets/8-2-lakshmi-picture.png';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

export default function App() {

  const [ data, setData ] = useState<{
    message: string
    rate: number
    state: number
    
  }>({message: '', rate: 0, state: 0});

  const rows: any = [];
  const refreshRate = () => {

    axios.get(`http://viewbcastgold.dpgold.in:8811/VOTSBroadcast/Services/xml/GetLiveRate?_=${new Date().getTime()}`).then((resp: any) => {
      // console.log(resp.data.split('M\t'))

      const message = resp.data.split('\r\n').find((m: any) => m.split('\t').findIndex((e: any) => e==="HYDERABAD") > -1 );
      const rate = parseFloat(message.split('\t')[2]);
      setData(s => ({...s, message, rate}));
      
    }).catch((err) => {
      console.log(err)
    })
  }

  useEffect(() => {

    setInterval(() => {
      refreshRate();
    }, 5000)
    
  }, [])

  return (
    <Container maxWidth="sm">
      <Box my={4}>
        
        <div className="flex pb-5">
          <div className="md:w-1/3">
            <img src={logo} height="200px" width="auto"></img>
          </div>
          <div className="ms:w-2/3">
          <Typography variant="h2" component="h1" color={'primary'}>
            శ్రీ లక్ష్మీ ప్రసన్న &nbsp; &nbsp; &nbsp;జ్యుయలర్స్        </Typography>
          </div>
        </div>
        
        <TableContainer component={Paper}>
      <Table  aria-label="simple table">
        <TableHead className="golden-background">
          <TableRow>
            <TableCell align="center" style={{ fontWeight: 600}}>10 Gram</TableCell>
            <TableCell align="center" style={{ fontWeight: 600}}> 1 Gram</TableCell>
            
            {/* <TableCell align="right">Fat&nbsp;(g)</TableCell>
            <TableCell align="right">Carbs&nbsp;(g)</TableCell>
            <TableCell align="right">Protein&nbsp;(g)</TableCell> */}
          </TableRow>
        </TableHead>
        <TableBody>
          
            <TableRow>
              <TableCell align="center" style={{ fontWeight: 600}}>{ (data?.rate + 25 ) * 10 }  
              {/* {data.state === 2 ?  <ImArrowDownRight/>  : null}  
              {data.state === 1 ?  <ImArrowUpRight/>  : null}   */}
              </TableCell>
              <TableCell align="center" style={{ fontWeight: 600}}>{ (data?.rate + 25 ) } 
              {/* {data.state === 2 ?  <ImArrowDownRight/>  : null}  
              {data.state === 1 ?  <ImArrowUpRight/>  : null}  */}
               </TableCell>
              
            </TableRow>
          
        </TableBody>
      </Table>
    </TableContainer>
            <div className="pt-5" />
        <Copyright/>
      </Box>
    </Container>
  );
};
